import React from 'react';
import {
  Select,
  makeStyles,
  MenuItem,
  Switch,
  FormGroup,
  FormControlLabel,
  withStyles,
} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { green } from '@material-ui/core/colors';
import {
  selectColorSchema,
  getColorSchema,
  getHasGreen,
  setHasGreen,
  getLightbar,
} from './lightbar-selector-slice';
import {
  setSchema,
  setSchemaName,
  getSchemaName,
} from '../LightbarSchema/lightbar-shema-slice';

const useStyles = makeStyles((theme) => ({
  select: {
    width: '100%',
    marginTop: theme.spacing(2),
  },
  switch: {
    marginTop: theme.spacing(2),
  },
}));

const GreenSwitch = withStyles({
  switchBase: {
    color: green[300],
    '&$checked': {
      color: green[500],
    },
    '&$checked + $track': {
      backgroundColor: green[500],
    },
  },
  checked: {},
  track: {},
})(Switch);

function ColorSchemaList() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const selectedSchema = useSelector(getSchemaName);
  const hasGreen = useSelector(getHasGreen);
  const lightbar = useSelector(getLightbar);
  const colorSchema = JSON.parse(
    localStorage.getItem('lightbar-schema') || '{}',
  );

  const onChange = (e) => {
    dispatch(setSchemaName(e.target.value));
    dispatch(setSchema(colorSchema[e.target.value] || []));
  };
  const onGreenSwithChange = (e) => dispatch(setHasGreen(e.target.checked));

  return (
    <>
      <Select
        value={selectedSchema || ''}
        onChange={onChange}
        displayEmpty
        className={classes.select}
        inputProps={{ 'aria-label': 'Without label' }}
      >
        <MenuItem value="create-new-schema">
          <em>Create a new one</em>
        </MenuItem>
        {Object.keys(colorSchema).map(
          (schema) =>
            lightbar &&
            lightbar.total === colorSchema[schema].length && (
              <MenuItem key={schema} value={schema}>
                {schema}
              </MenuItem>
            ),
        )}
      </Select>
      {/*
      <FormGroup>
        <FormControlLabel
          className={classes.switch}
          control={
            <GreenSwitch
              name="hasGreen"
              onChange={onGreenSwithChange}
              value={hasGreen}
            />
          }
          label="Includes Green Light"
        />
      </FormGroup>
      */}
    </>
  );
}

export default ColorSchemaList;
