import React from 'react';
import { List, makeStyles, ListItem, ListItemText } from '@material-ui/core';

import { useSelector, useDispatch } from 'react-redux';
import { LIGHTBARS } from './constants';
import { getLightbar, selectLightbar } from './lightbar-selector-slice';
import { setSchema } from '../LightbarSchema/lightbar-shema-slice';

const useStyles = makeStyles({
  list: {
    border: 'solid 1px lightgray',
    borderRadius: 3,
    marginTop: 10,
    marginBottom: 10,
    maxHeight: 150,
    overflow: 'auto',
  },
});

const ListItems = ({ onClick, selected = null }) =>
  LIGHTBARS.map((lightbar) => (
    <ListItem
      selected={selected?.model === lightbar.model}
      key={lightbar.model}
      onClick={() => onClick(lightbar)}
      button
    >
      <ListItemText
        primary={lightbar.model}
        secondary={`Front ${lightbar.front} / Rear ${lightbar.rear}`}
      />
    </ListItem>
  ));

function LightbarList() {
  const classes = useStyles();
  const selectedModel = useSelector(getLightbar);
  const dispatch = useDispatch();
  const onClick = (model) => {
    dispatch(selectLightbar(model));
    dispatch(setSchema(null));
  };

  return (
    <List className={classes.list}>
      <ListItems onClick={onClick} selected={selectedModel} />
    </List>
  );
}

export default LightbarList;
