import React from 'react';
import {
  List,
  makeStyles,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Typography,
  TextField,
} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { useSelector, useDispatch } from 'react-redux';
import {
  getCycles,
  getSelectedCycle,
  setSelectedCycle,
  deleteCycle,
  setCycleRepeat,
} from '../ligther-slice';
import LightbarPreview from '../../LightbarPreview';

const useStyles = makeStyles({
  root: {
    width: '100%',
    overflow: 'auto',
    maxHeight: 400,
  },
  item: {
    height: 150,
  },
  itemText: {
    alignItems: 'center',
    display: 'flex',
    flexDirection: 'column',
  },
  itemTextFirstRow: {
    alignSelf: 'normal',
    borderBottom: '1px solid lightgray',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    paddingBottom: 5,
  },
});

const Cycle = ({
  className,
  id,
  lights,
  onClick,
  onDelete,
  onRepeatChange,
  repeat,
  selected,
}) => (
  <ListItem
    alignItems="center"
    className={className.item}
    selected={selected}
    onClick={onClick}
    button
  >
    <ListItemText>
      <div className={className.itemText}>
        <div className={className.itemTextFirstRow}>
          <Typography component="span" variant="h6">
            {`Cycle #${id + 1}`}
          </Typography>
          <TextField
            label="Repeat"
            inputProps={{ max: 999, min: 0 }}
            min={1}
            onChange={onRepeatChange}
            size="small"
            type="number"
            value={repeat}
            variant="outlined"
          />
        </div>
        <LightbarPreview lights={lights} width={(lights.length - 6) * 10} />
      </div>
    </ListItemText>
    <ListItemSecondaryAction>
      <IconButton arial-label="delete" edge="end" onClick={onDelete}>
        <DeleteIcon />
      </IconButton>
    </ListItemSecondaryAction>
  </ListItem>
);

function CycleList() {
  const classes = useStyles();
  const cycles = useSelector(getCycles);
  const selectedCycle = useSelector(getSelectedCycle);
  const dispatch = useDispatch();

  const onClick = (id) => () => dispatch(setSelectedCycle(id));
  const onDelete = (id) => () => dispatch(deleteCycle(id));
  const onRepeatChange = (id) => (e) =>
    dispatch(setCycleRepeat({ id, repeat: e.target.value }));

  return (
    <List className={classes.root}>
      {cycles.map((c, i) => (
        <Cycle
          className={{
            item: classes.item,
            itemText: classes.itemText,
            itemTextFirstRow: classes.itemTextFirstRow,
          }}
          key={i}
          id={i}
          lights={c.lights}
          onClick={onClick(i)}
          onDelete={onDelete(i)}
          onRepeatChange={onRepeatChange(i)}
          repeat={c.repeat}
          selected={i === selectedCycle}
        />
      ))}
    </List>
  );
}

export default CycleList;
