import React, { useEffect } from 'react';
import Konva from 'konva';
import { makeStyles } from '@material-ui/core/styles';
import { useSelector, useDispatch } from 'react-redux';
import { Redirect } from 'react-router-dom';

import { useMediaQuery } from '@material-ui/core';
import LightbarPreview from '../LightbarPreview';
import LivePreview from '../LightbarPreview/live-preview';
import LighbarSetting from './LightbarSetting';
import Cycles from './Cycles';
import {
  getLightbar,
  getColorSchema,
} from '../LightbarSelector/lightbar-selector-slice';
import {
  getSelectedCycle,
  setLightbar,
  addCycle,
  getCycles,
  setSelectedLight,
  getSelectedLight,
  setLightSetting,
  getShowPreview,
  getLatestSelectedColor,
} from './ligther-slice';
import { getSchema } from '../LightbarSchema/lightbar-shema-slice';
import { rgbToHex } from '../../app/utils';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    marginTop: 20,
    padding: '0 16px',
    alignContent: 'space-between',
    flexWrap: (props) => (props.mqMatches ? null : 'wrap-reverse'),
  },
  cycles: {
    minWidth: 400,
    flexGrow: 1,
    bottom: 0,
  },
  details: {
    flexGrow: 2,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    alignContent: 'stretch',
    padding: '0 10px',
  },
});

function Lightbar() {
  const mqMatches = useMediaQuery('(min-width: 800px)');
  const classes = useStyles({ mqMatches });
  const dispatch = useDispatch();
  const selectedModel = useSelector(getLightbar);
  const selectedCycle = useSelector(getSelectedCycle);
  const selectedLight = useSelector(getSelectedLight);
  const cycles = useSelector(getCycles);
  const lightbar = useSelector(getLightbar);
  const showPreview = useSelector(getShowPreview);
  const colorSchema = useSelector(getSchema);
  const latestSelectedColor = useSelector(getLatestSelectedColor);

  const selectLight = (id) =>
    dispatch(
      setSelectedLight({
        id: [id],
        shouldSetColor:
          colorSchema[id].indexOf(rgbToHex(latestSelectedColor)) > -1,
      }),
    );
  const setLightChange = (change) => dispatch(setLightSetting(change));

  useEffect(() => {
    dispatch(setLightbar(selectedModel));
  }, []);

  useEffect(() => {
    !!lightbar && dispatch(addCycle());
  }, [lightbar]);

  if (!selectedModel) {
    return <Redirect to="/" />;
  }

  return (
    <>
      {!!lightbar && (
        <div className={classes.root}>
          <div className={classes.cycles}>
            <Cycles />
          </div>
          <div className={classes.details}>
            <LightbarPreview
              lights={cycles[selectedCycle]?.lights}
              onClick={selectLight}
              selectedLight={selectedLight}
              title={selectedModel.model}
            />
            <LighbarSetting
              config={cycles[selectedCycle]?.lights[selectedLight]}
              colors={colorSchema[selectedLight]}
              onChange={setLightChange}
            />
          </div>
        </div>
      )}
      {showPreview && <LivePreview />}
    </>
  );
}

export default Lightbar;
