import React, { useState, useEffect } from 'react';
import {
  makeStyles,
  Dialog,
  useMediaQuery,
  useTheme,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
} from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';
import {
  getShowPreview,
  setShowPreview,
  getCycles,
} from '../Lightbar/ligther-slice';
import LightbarPreview from '.';

const useStyles = makeStyles({});

function LivePreview() {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = useState(false);
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const showPreview = useSelector(getShowPreview);
  const cycles = useSelector(getCycles);
  const dispatch = useDispatch();
  const [visibleCycle, setVisibleCycle] = useState(0);
  const [repeat, setRepeat] = useState(1);

  useEffect(() => {
    const interval = setInterval(() => {
      if (
        cycles[visibleCycle].repeat > 1 &&
        repeat < cycles[visibleCycle].repeat
      ) {
        setRepeat(repeat + 1);
      } else {
        setVisibleCycle(
          visibleCycle + 1 < cycles.length ? visibleCycle + 1 : 0,
        );
        setRepeat(1);
      }
    }, 50);
    return () => clearInterval(interval);
  });

  const handleClose = () => dispatch(setShowPreview(false));

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={showPreview}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">Lightbar Preview</DialogTitle>
        <DialogContent>
          {cycles && cycles[visibleCycle] && (
            <LightbarPreview lights={cycles[visibleCycle].lights} />
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} variant="outlined">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default LivePreview;
