import React from 'react';
import _ from 'lodash';
import { useSelector, useDispatch } from 'react-redux';
import {
  Card,
  CardContent,
  Button,
  makeStyles,
  FormControlLabel,
  Checkbox,
  CardActions,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  TextField,
  DialogActions,
} from '@material-ui/core';
import { Redirect, useHistory } from 'react-router-dom';
import clsx from 'clsx';
import { getDefaultLights } from '../../app/utils';
import { getLightbar } from '../LightbarSelector/lightbar-selector-slice';
import LightbarPreview from '../LightbarPreview';
import {
  setLightbar,
  getSchemaName,
  getSchema,
  setSchema,
} from './lightbar-shema-slice';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexFlow: 'column',
  },
  lightbar: {
    margin: '20px 10px 0 10px',
    textAlign: 'center',
    display: 'inline-block',
    '& div': {},
  },
  lightbarContent: {
    display: 'flex',
    justifyContent: 'center',
  },
  settings: {
    marginTop: 10,
    display: 'flex',
    flexDirection: 'column',
    width: 600,
    alignSelf: 'center',
  },
  selectionButtons: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    '& button': {
      margin: 5,
    },
  },
  colors: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
  },
  checkbox: {
    width: 20,
    height: 20,
    borderRadius: 50,
    border: '2.5px solid rgba(0, 0, 0, 0.25)',
  },
  selectedCheckbox: {
    width: 20,
    height: 20,
    border: '2.5px solid',
    borderRadius: 50,
    borderColor: 'rgba(0, 0, 0, 0.75)',
  },
  red: {
    backgroundColor: '#FF0000',
  },
  green: {
    backgroundColor: '#00FF00',
  },
  blue: {
    backgroundColor: '#0000FF',
  },
  amber: {
    backgroundColor: '#FFBF00',
  },
  white: {
    backgroundColor: '#FFFFFF',
  },
  cardActions: {
    justifyContent: 'flex-end',
  },
});

const LightbarSchema = () => {
  const classes = useStyles();
  const history = useHistory();
  const lightbar = useSelector(getLightbar);
  const dispatch = useDispatch();
  const name = useSelector(getSchemaName);
  const [selectedLight, setSelectedLight] = React.useState([]);
  const [isDialogOpen, setDialogState] = React.useState(false);
  const [schemaName, setSchemaName] = React.useState(name);
  const lightColors = useSelector(getSchema);

  React.useEffect(() => {
    dispatch(setLightbar(lightbar));
  }, []);

  if (!lightbar) {
    return <Redirect to="/lightbars" />;
  }

  const handleClick = (id) => {
    const index = selectedLight.indexOf(id);
    if (index > -1) {
      selectedLight.splice(index, 1);
    } else {
      selectedLight.push(id);
    }
    setSelectedLight([...selectedLight]);
  };

  const lights = getDefaultLights(lightbar);

  const clearSelectedLight = (sectionLights) => {
    console.log(sectionLights);
    const schema = _.cloneDeep(lightColors);
    sectionLights.forEach((l) => {
      schema[l] = [];
    });
    dispatch(setSchema(schema));
  };

  const selectAllFront = () => {
    const sectionLights = [...new Array(lightbar.front).keys()];
    setSelectedLight(sectionLights);
    clearSelectedLight(sectionLights);
  };

  const selectAllRear = () => {
    const sectionLights = [...new Array(lightbar.rear).keys()].map(
      (i) => i + lightbar.front + 3,
    );
    setSelectedLight(sectionLights);
    clearSelectedLight(sectionLights);
  };

  const selectAllPassengerCorner = () => {
    const sectionLights = [...new Array(3).keys()].map(
      (i) => i + lightbar.front,
    );
    setSelectedLight(sectionLights);
    clearSelectedLight(sectionLights);
  };

  const selectAllDriverCorner = () => {
    const sectionLights = [...new Array(3).keys()].map(
      (i) => i + lightbar.front + lightbar.rear + 3,
    );
    setSelectedLight(sectionLights);
    clearSelectedLight(sectionLights);
  };

  const selectColor = (color) => () => {
    const schema = _.cloneDeep(lightColors);
    selectedLight.forEach((l) => {
      if (schema[l].indexOf(color) < 0) {
        schema[l].push(color);
      } else {
        schema[l].splice(schema[l].indexOf(color), 1);
      }
    });
    dispatch(setSchema(schema));
  };

  const isChecked = (color) =>
    selectedLight.length === 1 && lightColors[selectedLight[0]]
      ? lightColors[selectedLight[0]].indexOf(color) > -1
      : false;

  const goForward = () => setDialogState(true);
  const goBack = () => history.goBack();
  const closeDialog = () => setDialogState(false);
  const handleSchemaNameChange = (e) => setSchemaName(e.target.value);
  const goToLightbarSetting = () => history.push('/lightbar');
  const saveSchema = () => {
    const schemas = JSON.parse(localStorage.getItem('lightbar-schema') || '{}');
    schemas[schemaName] = lightColors;
    localStorage.setItem('lightbar-schema', JSON.stringify(schemas));
    goToLightbarSetting();
  };

  return (
    <div className={classes.root}>
      <Card className={classes.lightbar}>
        <CardContent className={classes.lightbarContent}>
          <LightbarPreview
            lights={lights}
            onClick={handleClick}
            overlays={lightColors}
            selectedLight={selectedLight}
          />
        </CardContent>
      </Card>
      <Card className={classes.settings}>
        <CardContent>
          <div className={classes.selectionButtons}>
            <Button variant="outlined" onClick={selectAllFront}>
              All Front
            </Button>
            <Button variant="outlined" onClick={selectAllRear}>
              All Rear
            </Button>
            <Button variant="outlined" onClick={selectAllPassengerCorner}>
              All Passenger Corner
            </Button>
            <Button variant="outlined" onClick={selectAllDriverCorner}>
              All Driver Corner
            </Button>
          </div>
          <div className={classes.colors}>
            <Checkbox
              icon={<span className={clsx(classes.checkbox, classes.red)} />}
              checked={isChecked('#FF0000')}
              checkedIcon={
                <span className={clsx(classes.selectedCheckbox, classes.red)} />
              }
              onChange={selectColor('#FF0000')}
            />
            <Checkbox
              icon={<span className={clsx(classes.checkbox, classes.green)} />}
              checkedIcon={
                <span
                  className={clsx(classes.selectedCheckbox, classes.green)}
                />
              }
              onChange={selectColor('#00FF00')}
            />
            <Checkbox
              icon={<span className={clsx(classes.checkbox, classes.blue)} />}
              checkedIcon={
                <span
                  className={clsx(classes.selectedCheckbox, classes.blue)}
                />
              }
              onChange={selectColor('#0000FF')}
            />
            <Checkbox
              icon={<span className={clsx(classes.checkbox, classes.amber)} />}
              checkedIcon={
                <span
                  className={clsx(classes.selectedCheckbox, classes.amber)}
                />
              }
              onChange={selectColor('#FFBF00')}
            />
            <Checkbox
              icon={<span className={clsx(classes.checkbox, classes.white)} />}
              checkedIcon={
                <span
                  className={clsx(classes.selectedCheckbox, classes.white)}
                />
              }
              onChange={selectColor('#FFFFFF')}
            />
          </div>
        </CardContent>
        <CardActions className={classes.cardActions}>
          <Button onClick={goBack} variant="outlined">
            Back
          </Button>
          <Button color="primary" onClick={goForward} variant="contained">
            Next
          </Button>
        </CardActions>
      </Card>
      <Dialog open={isDialogOpen} onClose={closeDialog}>
        <DialogTitle>Save Schema</DialogTitle>
        <DialogContent>
          <DialogContentText>
            If you want to save the schema to use it later, please enter a name
            for it.
          </DialogContentText>
          <TextField
            autoFocus
            fullWidth
            id="schemaName"
            label="Schema Name"
            margin="dense"
            value={schemaName === 'create-new-schema' ? '' : schemaName}
            onChange={handleSchemaNameChange}
          />
        </DialogContent>
        <DialogActions>
          <Button color="primary" onClick={goToLightbarSetting}>
            Continue without save
          </Button>
          <Button
            color="primary"
            onClick={saveSchema}
            disabled={!schemaName || schemaName === 'create-new-schema'}
          >
            Save and Continue
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

LightbarSchema.propTypes = {};

export default LightbarSchema;
