import Font from './Fonts/5x7-font';

export const textToBitmap = (text, color = [0, 255, 0], padding = 0) => {
  const bitMap = [];

  // [...Array(Font.row).keys()].forEach((r) =>
  //   [...Array(Font.column).keys()].forEach(() => {
  //     bitMap[r] = [];
  //     text.split('').forEach((char) => {
  //       bitMap[r].push(
  //         ...Font.chars[char][r]
  //           .toString(2)
  //           .padStart(char !== ' ' ? 5 : 1, 0)
  //           .split('')
  //           .map((v) => (+v === 1 ? color : [0, 0, 0])), // Adding the char in binary
  //         ...[...Array(padding).keys()].map(() => 0), // Adding padding to right
  //       );
  //     });
  //   }),
  // );

  const textArray = text.split('');

  [...Array(Font.column * text.length + text.length * padding).keys()].forEach(
    (c) => {
      bitMap[c] = [];

      if (
        padding &&
        c % (Font.column + padding) === Font.column + padding - 1
      ) {
        [...Array(Font.row).keys()].map(() => bitMap[c].push([0, 0, 0]));
      } else {
        [...Array(Font.row).keys()].forEach((r) => {
          const char = textArray[Math.floor(c / (Font.column + padding))];
          // text.split('')[text.length%column].forEach((char) => {
          bitMap[c].push(
            Font.chars[char][r]
              .toString(2)
              .padStart(char !== ' ' ? 5 : 1, 0)
              .split('')[c % (Font.column + padding)] === '1'
              ? color
              : [0, 0, 0],
            // .map((v) => (+v === 1 ? color : [0, 0, 0])), // Adding the char in binary
          );
          // });
        });
      }
    },
  );

  // console.log(bitMap);
  return bitMap;
};
