// eslint-disable no-parameter-reassign

import { createSlice } from '@reduxjs/toolkit';

const getDefaultSchema = (lightbar) => {
  const schema = [];
  if (lightbar) {
    for (let i = 0; i < lightbar.total; i += 1) {
      schema.push([]);
    }
  }
  return schema;
};

const slice = createSlice({
  name: 'lightbarSchema',
  initialState: {
    lightbar: null,
    name: null,
    schema: null,
  },
  reducers: {
    setLightbar: (state, action) => {
      state.lightbar = action.payload;
      if (!state.schema || !state.schema.length) {
        state.schema = getDefaultSchema(state.lightbar);
      }
    },
    setSchemaName: (state, action) => {
      state.name = action.payload;
    },
    setSchema: (state, action) => {
      state.schema = action.payload;
      if (!action.payload) {
        state.name = null;
      }
    },
  },
});

export const { setLightbar, setSchemaName, setSchema } = slice.actions;

export const getSchemaState = (state) => state.lightbarSchema;
export const getLightbar = (state) => state.lightbarSchema.lightbar;
export const getSchemaName = (state) => state.lightbarSchema.name;
export const getSchema = (state) => state.lightbarSchema.schema;

export default slice.reducer;
